import { create } from "zustand";
export const useSecretSantaStore = create((set) => ({
    snowRate: 100,
    participantsDetails: [],
    setSnowRate: (value) => set(() => ({ snowRate: value })),
    setParticipantsDetails: (newParticipantsDetails) => set(() => ({ participantsDetails: newParticipantsDetails }))
}));
